html,body{
  background-color: #ebebeb;
}

*{
  margin: 0;
  padding: 0;
  list-style: none;
}
a{
  text-decoration: none;
}
input{
  border: none;
  outline: none;
}
.red{
  color: #fd2754;
}
.gray{
  color: #b3b3b3;
}
.gold{
  color: #ffe4bd;
}
.green{
  color: #3e9a34;
}

::-webkit-input-placeholder{
  color: #ccc;
}

/* header */
.header{
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  padding:0 0.37rem 0 1.2rem;
  height: 1.36rem;
  background-color: #fd2754;
  color: #fff;
  font-size: 0.42rem;
  background-image: url("./static/back.png");
  background-repeat: no-repeat;
  background-position: 0.35rem center;
  background-size: 0.53rem 0.51rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  z-index: 10;
}
.header .touchBack{
  position: absolute;
  left: 0;
  top: 0;
  width: 2.2rem;
  height: 100%;
}

.header .saveChange{
  display: block;
  height: 1.36rem;
  line-height: 1.36rem;
}
/* header */

/* choose */
.chooseComponent{
  background-color: #fff;
}
.chooseComponent .item{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  padding: 0 0.5rem;
  height: 1rem;
  font-size: 0.42rem;
  border-bottom: 1px solid #e6e6e6;
}
.chooseComponent .item:last-child{
  border: none;
}
.chooseComponent .item .right{
  display: none;
  width: 0.42rem;
  height: 0.42rem;
  background: url("./static/right.png") no-repeat center;
  background-size: contain;
}
.chooseComponent .item.active .right{
  display: block;
}
/* choose */

/* tabbar */
.tabbar{
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1.4rem;
  border-top: 0.01rem solid #ffe5eb;
  background-color: #fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  font-size: 0.3rem;
}
.tabbar .item{
  width: 25%;
  color: #4d4d4d;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.tabbar .item.active{
  color: #ff5d87;
}
.tabbar .item .icon{
  margin-bottom: 0.1rem;
  width: 0.54rem;
  height: 0.54rem;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
/* tabbar */

/*  */
.switch{
  position: relative;
  width: 1.2rem;
  height: 0.16rem;
  border-radius: 0.08rem;
  background-color: #ccc;
  -webkit-transition: cubic-bezier(0.4, 0.0, 0.2, 1) 0.2s;
  -o-transition: cubic-bezier(0.4, 0.0, 0.2, 1) 0.2s;
  transition: cubic-bezier(0.4, 0.0, 0.2, 1) 0.2s;
}
.switch .slider{
  position: absolute;
  left: 0;
  top: -0.25rem;
  width: 0.6rem;
  height: 0.6rem;
  border-radius: 50%;
  background-color: #e6e6e6;
  -webkit-transition: cubic-bezier(0.4, 0.0, 0.2, 1) 0.2s;
  -o-transition: cubic-bezier(0.4, 0.0, 0.2, 1) 0.2s;
  transition: cubic-bezier(0.4, 0.0, 0.2, 1) 0.2s;
}
.switch.open{
  background-color: #ff8da5;
}
.switch.open .slider{
  left: 0.6rem;
  background-color: #fa3d65;
}
/*  */

/*  */

.confirmBtn{
  margin: 0 auto;
  width: 8rem;
  height: 1rem;
  line-height: 1rem;
  text-align: center;
  font-size: 0.4rem;
  color: #fff;
  border-radius: 0.04rem;
  font-weight: lighter;
}
.confirmBtnDis{
  background-color: #dbdbdb;
}

.confirmBtnAbl{
  background-color: #fd2754;
}
/*  */

/*  */
.no-activity{
  position: relative;
  margin: 1rem auto;
  width: 9.1rem;
  height: 7.2rem;
  background: url("./static/no_activity.png") no-repeat center;
  background-size: contain;
}
.no-activity .no-des{
  padding-top: 1.2rem;
  text-align: center;
}
.no-activity .create{
  position: absolute;
  bottom: 1.3rem;
  left: 2.3rem;
  width: 4.6rem;
  height: 1.3rem;
  line-height: 1.3rem;
  text-align: center;
  font-size: 0.42rem;
  font-weight: 900;
  border-radius: 0.65rem;
  color: #fff;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#fb0f46), to(#fc5a5b));
  background-image: -webkit-linear-gradient(top, #fb0f46, #fc5a5b);
  background-image: -o-linear-gradient(top, #fb0f46, #fc5a5b);
  background-image: linear-gradient(to bottom, #fb0f46, #fc5a5b);
  -webkit-box-shadow: 0 0.04rem 0.2rem 0.04rem #ffb8bf;
          box-shadow: 0 0.04rem 0.2rem 0.04rem #ffb8bf;
}
/*  */


/*  */
.noAccess{
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding-top: 1.36rem;
  width: 100vw;
  height: 100vh;
  background-color: #fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: start;
      align-items: flex-start;
}
.noAccess img{
  margin-top: 3rem;
  width: 6rem;
}
