.chooseTypes{
    padding-top: 1.36rem;
}
.chooseTypes .items{
    background-color: #fff;
}
.chooseTypes .item{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 0.5rem;
    height: 1rem;
    font-size: 0.42rem;
    border-bottom: 1px solid #e6e6e6;
}
.chooseTypes .item:last-child{
    border: none;
}

.chooseTypes .item .right{
    display: none;
    width: 0.42rem;
    height: 0.42rem;
    background: url("../images/right.png") no-repeat center;
    background-size: contain;
}
.chooseTypes .item.active .right{
    display: block;
}


